.programs{
    color: white;
    padding: 24px 16px;
}
.programs-header{
    display: flex;
    gap: 80px;
    font-size: 48px;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
    font-weight: bold;
}
.programs-header span:nth-child(2){
    color: var(--primaryColor);
}

.programs-categories{
    display: flex;
    gap: 16px;
    padding: 1rem 0;
}
.category{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16px;
    padding: 16px;
    background: gray;
}
.category:hover{
    background: var(--planCard);
}
.category :nth-child(1){
    width: 32px;
    height: 32px;
    fill: white;
}
.category :nth-child(2){
    font-size: 16px;
    font-weight: bold;
}
.category :nth-child(3){
    font-size: 14px;
    line-height: 24px;
}
.join-now{
    display: flex;
    align-items: center;
    gap: 32px;
}
.join-now img{
    width: 10px;
    padding: 0 4px;
    margin-top: -6px;
}



@media (max-width: 768px){
    .programs-header{
        flex-direction: column;
        gap: 10px;
        font-size: 24px;
        text-align: center;
        margin: 1rem 0;
    }
    .programs-categories{
        flex-direction: column;
    }
}