.hero{
    display: flex;
    justify-content: space-between;
}
.blur-hero{
    width: 300px;
    height: 350px;
    left: 0;
}
.left-hero{
    padding: 24px;
    flex: 3;
    position: relative;
}
.best-ad{
    color: white;
    background-color: #393d42;
    padding: 16px 12px;
    margin-top: 48px;
    text-align: center;
    position: relative;
    width: fit-content;
    border-radius: 25px;
    z-index: 1;
    display: flex;
    align-items: center;
}
.best-ad div{
    width: 80px;
    height: 80%;
    /* background-color: orange; */
    background-color: var(--primaryColor);
    border-radius: 25px;
    left: 6px;
    position: absolute;
    z-index: -1;
}
.best-ad span{
    text-transform: uppercase;
}

.hero-text {
	color: #fff;
    gap: 24px;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    margin: 20px 0;
}
.hero-text span{
    font-size: 48px;
    text-transform: uppercase;
}

.counting{
    display: flex;
    flex-wrap: wrap;
    gap: 48px;
    color: white;
    font-size: 32px;
}
.counting h6{
   margin: 0;
   text-transform: uppercase;
   color: gray;
}

.hero-button{
    gap: 16px;
    display: flex;
    flex-wrap: wrap;
    margin: 24px 0;
}
.hero-button :nth-child(1){
    background-color: var(--primaryColor);
    color: white;
    width: 120px;
}
.hero-button :nth-child(2){
    color: white;
    background-color: transparent;
    border: 2px solid var(--primaryColor);
    width: 120px;
}

.calories{
    position: absolute;
    background-color: var(--caloryCard);
    top: 420px;
    right: 200px;
    display: flex;
    width: fit-content;
    padding: 8px;
    border-radius: 4px;
    height: 100px;
}
.calories img{
    padding: 10px;
    width: 60px;
}
.calories div{
    padding: 8px;
}
.calories h3,.calories h4{
    margin: 0;
}
.calories h3{
    color: white;
}
.calories h4{
   color: gray;
}



.right-hero{
    background-color: var(--primaryColor);
    flex: 1;
    position: relative;
}
.right-hero .hero-image{
    width: 400px;
    position: absolute;
    top: 100px;
    left: -230px;
    z-index: 100;
}
.right-hero .hero-image-back{
    top: 100px;
    right: 20rem;
    position: absolute;
    z-index: -1;
}
.right-hero button{
    top: 20px;
    left: 30%;
    margin: 0 auto;
    position: absolute;
    background-color: coral;
    border-radius: 4px;
    padding: 6px 10px;
}
.right-hero .heart-rate{
    background-color: #393d42;
    padding: 10px 16px;
    width: fit-content;
    top: 70px;
    left: 80px;
    position: absolute;
    border-radius: 4px;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 1;
}
.right-hero .heart-rate img{
    width: 100px;
    height: auto;
}
.right-hero .heart-rate span{
    color: gray;
}
.right-hero .heart-rate h2{
    color: white;
}



@media (max-width: 768px){
    .hero{
        flex-direction: column;
        text-align: center;
    }
    .best-ad{
        padding: 8px 12px;
        text-align: center;
        align-items: center;
        justify-content: center;
        margin: 3rem auto;
    }
    .best-ad span{
        font-size: 14px;
        text-transform: capitalize;
        font-weight: bold;
    }
    .hero-text span{
        font-size: 32px;
    }
    .counting{
        font-size: 20px;
        text-align: center;
        justify-content: center;
        gap: 1rem;
    }
    .hero-button{
        justify-content: center;
    }
    .calories{
        position: initial;
        justify-content: center;
        margin: 0 auto;
    }



        
    .right-hero{
        background: none;
        position: relative;
    }
    .right-hero .heart-rate{
        left: 8px;
        top: 1rem;
    }
    .right-hero .heart-rate h2{
        color: white;
        font-size: 14px;
    }
    .right-hero .hero-image{
        width: 300px;
        position: relative;
        top: 2rem;
        left: 0;
    }
    .right-hero .hero-image-back{
        width: 11rem;
        left: 300px;
        top: 5rem;
    }

    .right-hero button{
        left: 0;
        position: relative;
    }
   

}

