:root{
  --caloryCard: #656565;
  /* --planCard: linear-gradient(210.41deg, #fa5042 1.14%, #ffa735 100.75%); */
  --planCard: linear-gradient(210.41deg, #0d9db6 1.14%, #4935ff 100.75%);
  --appColor: #3c3f45;
  --primaryColor: cornflowerblue;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--appColor);
  display: flex;
  flex-direction: column;
  /* gap: 6rem; */
  gap: 2rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

.stroke-text {
	-webkit-text-stroke-width: 1px;
	-webkit-text-stroke-color: #fff;
	color: transparent;
	font-family: Arial,Helvetica,sans-serif;
}

.btn {
	background-color: #fff;
	border: 4px solid transparent;
	font-weight: bold;
	padding: .5rem;
	transition: all .3s;
  cursor: pointer;
}

.blur{
  /* background: rgba(252, 116, 38, 0.7); */
  background: rgba(11, 194, 240, 0.7);
  position: absolute;
  border-radius: 50%;
  filter: blur(140px);
  z-index: -10;
}




