.reasons{
    display: flex;
    padding: 0 16px;
    gap: 1rem;
}
.left-reason{
    flex: 1;
}
.left-reason img{
    width: 100%;
}



.right-reason{
    flex: 1;
    padding: 0 1rem;
}
.right-reason h5{
    color: var(--primaryColor);
    font-weight: bold;
    text-transform: uppercase;
}
.right-reason h2{
    color: white;
    font-weight: bold;
    text-transform: uppercase;
}
.right-reason .details-right img{
    width: 1rem;
}
.right-reason .details-right span{
    padding: 0 16px;
    color: white;
} 
.right-reason .partners img{
    width: 3rem;
}


@media (max-width: 768px){
    .reasons{
        flex-direction: column;
    }
}