.footer{
    padding: 1rem;
    position: relative;
}
.blur-footer-1{
    bottom: 0;
    width: 200px;
    height: 150px;
    filter: blur(170px);
    /* background: orangered; */
    background: rgba(11, 194, 240, 0.7);
    left: 30%;
}
.blur-footer-2{
    bottom: 0;
    width: 200px;
    height: 150px;
    filter: blur(170px);
    /* background: orange; */
    background: rgba(226, 88, 219, 0.7);
    right: 30%;
}
.footer-container{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    justify-content: center;
}
.footer-container .social-link{
    display: flex;
    gap: 2rem;
    margin-top: 2rem;
}
.footer-container .social-link img{
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
.footer-container .logo-footer img{
    width: 200px;
}