.header{
    display: flex;
    justify-content: space-between;
}
.logo{
    width: auto;
    height: 48px;
}
.header-menu{
    list-style: none;
    color: white;
    display: flex;
    gap: 16px;
}
.header-menu li:hover{
    cursor: pointer;
    color: orange;
}

.header .menu-bar{
    cursor: pointer;
    height: 100%;
    background-color: var(--appColor);
    border-radius: 4px;
}
.header .menu-bar img{
    width: 20px;
    height: 17px;
    padding: 6px;
}

@media (max-width: 768px){
    .header > :nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 1000;
        height: auto;
    }
    .header-menu{
        flex-direction: column;
        background-color: var(--appColor);
        margin: 0;
        padding: 1rem;
    }
    .header-menu li{
        text-align: left;
    }
}