.join{
    display: flex;
    gap: 1rem;
    padding: 1rem;
    position: relative;
}
.left-j{
    flex: 1;
}
.left-j span{
    font-weight: bold;
    font-size: 2rem;
    text-transform: uppercase;
}


.right-j{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.right-j form{
    background-color: var(--caloryCard);
    width: fit-content;
    padding: 8px 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 1rem;
}
.right-j form input{
    background-color: transparent;
    border: none;
    color: white;
    padding: 8px 4px;
}
.right-j form input::placeholder{
    color: lightgray;
}
.right-j form button{
    background-color: var(--primaryColor);
    color: white;
}


@media (max-width: 768px){
    .join{
        flex-direction: column;
        margin-top: 15rem;
        text-align: center;
    }
    .left-j span{
        font-size: 24px;
        text-align: center;
    }
}