
.plans-container {
    padding: 0 16px;
    position: relative;
}
.blur-plans-1{
    width: 400px;
    height: 300px;
    left: 0;
    top: 0;
}
.blur-plans-2{
    width: 400px;
    height: 300px;
    right: 0;
    bottom: 0;
}
.plans-container .plans{
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    margin: 32px 0;
}
.plans-container .plans .plan{
    display: flex;
    flex-direction: column;
    background: var(--caloryCard);
    color: white;
    padding: 16px;
    width: 250px;
    gap: 16px;
}
.plans-container .plan:nth-child(2){
    background: var(--planCard);
    transform: scale(1.1);
}
.plan>:nth-child(1){
    fill: var(--primaryColor);
}
.plan>:nth-child(2){
    font-weight: bold;
}
.plan>:nth-child(3){
    font-size: 32px;
    font-weight: bold;
}
.plan>:nth-child(5){
    font-size: 12px;
    cursor: pointer;
}
.feature{
    display: flex;
    align-items: center;
    gap: 16px;
}
.feature img{
    width: 16px;
}
.plans :nth-child(2) svg{
    fill: white;
}
.plans :nth-child(2) button{
    color: var(--primaryColor);
}


@media (max-width:768px){
    .plans-container .plans{
        flex-direction: column;
        gap: 1rem;
    }
    .plans-container .plan:nth-child(2){
        background: var(--planCard);
        transform: scale(1);
    }
}