.testimonials{
    padding: 0 16px;
    display: flex;
    gap: 1rem;
    margin-bottom: 50px;
}
.left-t{
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    text-transform: uppercase;
}
.left-t .header-t{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    flex-direction: column;
}
.left-t .header-t h4{
    color: lightgray;
}

.left-t .header-t span{
    font-size: 32px;
    font-weight: bold;
}
.left-t .header-t span:nth-child(3){
    color: var(--primaryColor);
}
.left-t p{
    color: white;
    text-transform: none;
}


.right-t{
    flex: 1;
    position: relative;
}
.right-t>:nth-child(1){
    width: 200px;
    height: 220px;
    top: 1rem;
    right: 11rem;
    position: absolute;
    background: var(--planCard);
}
.right-t>img{
    width: 200px;
    height: 220px;
    top: 0rem;
    right: 12rem;
    position: absolute;
    object-fit: cover;
}
.arrows{
    bottom: 1rem;
    left: 3rem;
    position: absolute;
    display: flex;
    gap: 1rem;
}
.arrows img{
    width: 24px;
    cursor: pointer;
}



@media (max-width: 768px){
    .testimonials{
        flex-direction: column;
    }
    .left-t .header-t span{
        font-size: 24px;
    }



        
    .right-t>:nth-child(1){
        width: 200px;
        height: 220px;
        top: 5rem;
        right: 2rem;
        position: absolute;
        background: var(--planCard);
    }
    .right-t>img{
        width: 200px;
        height: 220px;
        top: 4rem;
        right: 3rem;
        position: absolute;
        object-fit: cover;
    }
    .arrows{
        bottom: -2rem;
        left: 6rem;
        position: absolute;
        display: flex;
        gap: 1rem;

    }
    .arrows img{
        width: 16px;
        cursor: pointer;
    }

}